*.unselectable {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;

  /*
    Introduced in IE 10.
    See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
  */
  -ms-user-select: none;
  user-select: none;
}

.deselector {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.square {
  position: absolute;
}

.light {
  background-color: rgb(206, 179, 159);
}

.dark {
  background-color: saddlebrown;
}

.highlighted {
  background-color: yellow;
}

.piece {
  z-index: 1;
  position: absolute;
  cursor: pointer;
}
